* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.app {
  width: 100%;
  height: 100%;
  font-family: Arial, Helvetica, sans-serif;
}

.header {
  background-color: beige;
  border: 1px solid #000;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  display: grid;
}

.logo-img {
  place-self: start;
}

.logo-img > img {
  width: 150px;
  height: 75px;
  margin: 5px;
}

.nav-items-container {
  grid-template-columns: repeat(4, 1fr);
  justify-self: end;
  place-items: center;
  gap: .5rem;
  display: grid;
}

.nav-items {
  justify-content: center;
  align-items: center;
  margin: 0 25px;
  font-weight: 500;
  display: flex;
}

.nav-items > a {
  color: #000;
  text-decoration: none;
}

.nav-items > a:hover, .nav-items:hover {
  color: red;
  cursor: pointer;
}

.nav-item-cart {
  grid-template-columns: repeat(2, 1fr);
  place-items: center;
  gap: .1rem;
  margin: 0 25px;
  font-weight: 500;
  display: grid;
}

.nav-item-cart:hover {
  color: red;
  cursor: pointer;
}

.nav-item-cart:hover > div {
  background-color: red;
}

.body {
  background-color: beige;
  border: 1px solid #000;
  flex-wrap: wrap;
  justify-content: center;
  height: auto;
  display: flex;
}

.res-menu-container {
  flex-direction: column;
  justify-content: center;
  justify-self: center;
  width: 1275px;
  margin: 10px 50px;
  padding: 10px 250px;
  display: flex;
}

.res-menu-path {
  color: #789;
  justify-self: center;
  height: 25px;
  margin: 10px 0;
  font-size: 10px;
  display: flex;
}

.res-menu-path > span {
  margin: 0 2px;
}

.res-menu-name {
  margin: 10px 0;
  font-size: x-large;
  font-weight: 700;
}

.quick-info-card {
  border: 1px solid #000;
  border-radius: 20px;
  width: 770px;
  margin: 15px 0;
  padding: 15px;
  box-shadow: 0 4px 12px #0000001a, 0 10px 16px #0000000d;
}

.quick-info-first {
  align-items: center;
  margin: 7px 0;
  display: flex;
}

.quick-info-first > img {
  width: 20px;
  height: 20px;
  margin-right: 5px;
}

.quick-info-first > span {
  font-weight: 700;
}

.quick-info-first-dot {
  margin: 0 8px;
}

.quick-info-second {
  color: green;
  margin: 7px 0;
  font-weight: 600;
}

.quick-info-third {
  align-items: center;
  display: flex;
}

.quick-info-third > img {
  width: 25px;
  height: 25px;
  margin-right: 5px;
}

.quick-info-third > span {
  margin-right: 15px;
  font-weight: 700;
}

.quick-info-third > p {
  color: gray;
  margin: 0;
  font-size: small;
}

.quick-info-fourth {
  align-items: center;
  display: flex;
}

.quick-info-fourth > img {
  width: 15px;
  height: 15px;
  margin: 0 5px;
}

.quick-info-fourth > span {
  margin-left: 8px;
  font-size: medium;
  font-weight: 550;
}

.search-bar-container {
  background-color: #f4e4d1;
  border: 1px solid #000;
  flex-basis: 100%;
  justify-content: space-around;
  align-items: center;
  margin: 10px 5px;
  display: flex;
}

.search-bar-container > input {
  border: none;
  border-radius: 5px;
  flex-grow: 1;
  max-width: 300px;
  height: 30px;
  margin-left: 50px;
  padding: 5px;
}

.search-bar-btn {
  color: #fff;
  cursor: pointer;
  background-color: #25b45c;
  border: none;
  border-radius: 5px;
  justify-self: flex-start;
  margin: 5px;
  padding: 3px 6px;
  font-size: 16px;
  font-weight: 700;
}

.search-bar-btn:hover {
  transition: transform .3s;
  transform: scale(.9);
}

.res-card-container {
  grid-template-columns: repeat(1, 1fr);
  place-items: stretch center;
  gap: 20px;
  width: fit-content;
  padding: 18px;
  display: grid;
}

.res-card-container > a {
  color: #000;
  text-decoration: none;
}

.shimmer-container {
  grid-template-columns: repeat(1, 1fr);
  place-items: center;
  gap: 20px;
  width: fit-content;
  margin-left: 5px;
  padding: 15px;
  display: grid;
}

.shimmer-item {
  background-color: #f5f5f5;
  border: .5px solid #000;
  border-radius: 15px;
  flex-wrap: wrap;
  width: 330px;
  height: 280px;
  margin: 5px;
  padding: 8px;
  display: flex;
}

.shimmer-container-2 {
  grid-template-columns: repeat(1, 1fr);
  place-items: center;
  gap: 20px;
  width: fit-content;
  margin: 15px 0;
  display: grid;
}

.shimmer-item-2 {
  background-color: #f5f5f5;
  border: .5px solid #000;
  border-radius: 15px;
  flex-wrap: wrap;
  width: 770px;
  height: 180px;
  margin: 5px;
  padding: 8px;
  display: flex;
}

.res-card {
  background-color: #f5f5f5;
  border: .5px solid #000;
  border-radius: 15px;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
  margin: 5px;
  padding: 8px;
  display: flex;
}

.res-card p {
  flex-basis: 100%;
  margin: 8px 0;
  font-size: 16px;
}

.res-card:hover {
  cursor: pointer;
  border: .8px solid #000;
  transition: transform .3s;
  transform: scale(.95);
  box-shadow: 5px 5px 5px #4645454d;
}

.res-card-img {
  object-fit: cover;
  border-radius: 10px;
  flex-basis: 80%;
  justify-self: center;
  width: 240px;
  height: 140px;
  margin: 5px auto;
  display: block;
}

.footer {
  background-color: beige;
  border: 1px solid #000;
  justify-items: center;
  row-gap: 1rem;
  display: grid;
}

.app-down-container {
  grid-template-columns: repeat(2, 1fr);
  display: grid;
}

.app-down-promo {
  color: #585858;
  place-self: center end;
  padding: 20px;
  font-family: Arial, sans-serif;
  font-size: 2rem;
  font-weight: 700;
  line-height: 1.2;
}

.app-down-info {
  justify-content: start;
  align-items: center;
  display: flex;
}

.app-down-item {
  margin: 10px;
}

.app-down-item > img {
  width: 210px;
  height: 60px;
}

.app-info-container {
  grid-template-columns: repeat(4, 1fr);
  justify-items: stretch;
  gap: 10px 20px;
  width: fit-content;
  padding: 10px 0;
  display: grid;
}

.app-info-container > div {
  border: 1px solid #0000002e;
  border-radius: 15px;
  transition: box-shadow 1s, transform 1s;
  box-shadow: inset 4px 3px 5px #00000050;
}

.app-info-container > div:hover {
  box-shadow: 0 3px 0 1.5px #39bb39, .5em 1em .4em #00000081;
}

.company {
  grid-area: 1 / 1 / 3 / 2;
  height: auto;
}

.company-info > h1, p {
  margin: 10px;
}

.company-info > h1 > img {
  width: 30px;
  height: 30px;
}

.company-details-container > h2, .company-details-items {
  margin: 10px;
}

.company-details-items:hover {
  cursor: pointer;
}

.company-details-items > a {
  color: #000;
  text-decoration: none;
}

.company-details-items > a:hover {
  color: red;
  cursor: pointer;
}

.delivery-details-container > h2, .delivery-details-items, .content1-details-container > h2, .content1-details-items {
  margin: 10px;
}

.content1-details-items:hover {
  cursor: pointer;
}

.content1-details-items > a {
  color: #000;
  text-decoration: none;
}

.content1-details-items > a:hover {
  color: red;
  cursor: pointer;
}

.content2-details-container > h2, .content2-details-items {
  margin: 10px;
}

.content2-details-items:hover {
  cursor: pointer;
}

